<template>
  <transition name="modal-fade">
    <div class="modal-backdrop">
      <div class="modal-streamers"
         role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
      >
        <header class="modal-streamers-header">
          <slot name="header">
            ...
            <button
              type="button"
              class="btn-close"
              @click="close"
            >
              x
            </button>
          </slot>
        </header>
        <section class="modal-streamers-body">
          <slot name="body">
            <div class="modal-img-container">
              <img class="modal-img" :src="profile.img" />
              <span class="modal-name">{{ profile.name }}</span>
            </div>
            <div class="modal-description">
              <span class="modal-description-text">{{ profile.bio }}</span>
            </div>
          </slot>
        </section>
        <!--<footer class="modal-footer">
            <slot name="footer">
               I'm the default footer! 
              <button
                type="button"
                class="btn-green"
                @click="close"
              >
                Close me!
            </button>
          </slot>
        </footer>-->
      </div>
    </div>
   </transition>
</template>

<script>
  export default {
    name: 'modal',
    props: {
      profile: {
        required: true
      }
    },
    methods: {
      close() {
        this.$emit('close');
      },
    },
  };
</script>

<style>
  .modal-backdrop {
    position: fixed!important;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .modal-streamers {
    max-width: 500px;
    padding: 0 10px;
    background: #FFFFFF;
    box-shadow: 2px 2px 20px 1px;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
  }

  .modal-streamers-header,
  .modal-streamers-footer {
    /*padding: 5px;*/
    display: flex;
  }

  .modal-streamers-header {
    /*border-bottom: 1px solid #eeeeee;*/
    color: #4AAE9B;
    justify-content: space-between;
  }

  .modal-streamers-footer {
    /*border-top: 1px solid #eeeeee;*/
    justify-content: flex-end;
  }

  .modal-streamers-body {
    position: relative;
    padding: 20px 10px;
  }

  .btn-close {
    border: none;
    font-size: 20px;
    padding: 20px;
    cursor: pointer;
    font-weight: bold;
    color: #4AAE9B;
    background: transparent;
  }

  .btn-green {
    color: white;
    background: #4AAE9B;
    border: 1px solid #4AAE9B;
    border-radius: 2px;
  }
   .modal-fade-enter,
  .modal-fade-leave-active {
    opacity: 0;
  }

  .modal-fade-enter-active,
  .modal-fade-leave-active {
    transition: opacity .5s ease
  }
  .modal-img-container {
    width: 150px;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f2f8fc+0,e6eff7+51,dadfe5+100 */
    background: #f2f8fc; /* Old browsers */
    background: -moz-linear-gradient(-45deg,  #f2f8fc 0%, #e6eff7 51%, #dadfe5 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg,  #f2f8fc 0%,#e6eff7 51%,#dadfe5 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg,  #f2f8fc 0%,#e6eff7 51%,#dadfe5 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2f8fc', endColorstr='#dadfe5',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    -webkit-box-shadow: 8px 6px 14px -7px rgba(0,0,0,0.98);
    -moz-box-shadow: 8px 6px 14px -7px rgba(0,0,0,0.98);
    box-shadow: 8px 6px 14px -7px rgba(0,0,0,0.98);
    border-radius: 5px;
    padding: 15px 20px;
    float: left;
  }
  .modal-img {
    max-width:100%;
    padding:10px 5px;
    margin-bottom: 10px;
  }
  .modal-name {
    display: block;
  }
  .modal-description {
  }
  .modal-description-text {
    padding: 0 40px;
  }
</style>
